import React, { useState, useEffect } from 'react';
import { useApp } from '../context/AppContext';
import './Sales.css';

const Sales = () => {
  const { 
    sales,
    fetchSales,
    createSale,
    formatCurrency,
    showAlert
  } = useApp();

  const [showForm, setShowForm] = useState(false);
  const [formData, setFormData] = useState({
    productId: '',
    customerId: '',
    quantity: 1,
    price: 0,
    saleDate: new Date().toISOString().split('T')[0]
  });
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    loadSales();
  }, []);

  const loadSales = async () => {
    setLoading(true);
    await fetchSales();
    setLoading(false);
  };

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setFormData(prev => ({
      ...prev,
      [name]: name === 'quantity' || name === 'price' ? parseFloat(value) : value
    }));
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    if (!formData.productId || !formData.customerId || !formData.quantity) {
      showAlert('Please fill all required fields', 'error');
      return;
    }

    const success = await createSale(formData);
    if (success) {
      setFormData({
        productId: '',
        customerId: '',
        quantity: 1,
        price: 0,
        saleDate: new Date().toISOString().split('T')[0]
      });
      setShowForm(false);
      loadSales();
    }
  };

  const handleDelete = async (id) => {
    // Delete not implemented in API - placeholder
    showAlert('Delete not implemented', 'info');
  };

  return (
    <div className="sales-container">
      <div className="sales-header">
        <h2>Sales</h2>
        <button 
          className="btn btn-primary"
          onClick={() => setShowForm(!showForm)}
        >
          {showForm ? 'Cancel' : '+ New Sale'}
        </button>
      </div>

      {showForm && (
        <div className="sales-form">
          <h3>Record Sale</h3>
          <form onSubmit={handleSubmit}>
            <div className="form-row">
              <div className="form-group">
                <label>Product ID *</label>
                <input
                  type="text"
                  name="productId"
                  value={formData.productId}
                  onChange={handleInputChange}
                  required
                />
              </div>
              <div className="form-group">
                <label>Customer ID *</label>
                <input
                  type="text"
                  name="customerId"
                  value={formData.customerId}
                  onChange={handleInputChange}
                  required
                />
              </div>
            </div>

            <div className="form-row">
              <div className="form-group">
                <label>Quantity *</label>
                <input
                  type="number"
                  name="quantity"
                  value={formData.quantity}
                  onChange={handleInputChange}
                  min="1"
                  required
                />
              </div>
              <div className="form-group">
                <label>Price</label>
                <input
                  type="number"
                  name="price"
                  value={formData.price}
                  onChange={handleInputChange}
                  step="0.01"
                />
              </div>
            </div>

            <div className="form-group">
              <label>Sale Date</label>
              <input
                type="date"
                name="saleDate"
                value={formData.saleDate}
                onChange={handleInputChange}
              />
            </div>

            <button type="submit" className="btn btn-success">Record Sale</button>
          </form>
        </div>
      )}

      <div className="sales-table">
        {loading ? (
          <p>Loading sales...</p>
        ) : sales && sales.length > 0 ? (
          <table>
            <thead>
              <tr>
                <th>Sale #</th>
                <th>Customer</th>
                <th>Items</th>
                <th>Total</th>
                <th>Date</th>
              </tr>
            </thead>
            <tbody>
              {sales.map(sale => (
                <tr key={sale._id}>
                  <td>{sale.saleNumber || sale._id}</td>
                  <td>{sale.customer?.name || ''}</td>
                  <td>
                    {sale.items?.map(it => `${it.product?.name || it.product} x${it.quantity}`).join(', ')}
                  </td>
                  <td>{formatCurrency(sale.total)}</td>
                  <td>{new Date(sale.saleDate).toLocaleDateString()}</td>
                </tr>
              ))}
            </tbody>
          </table>
        ) : (
          <p>No sales recorded yet.</p>
        )}
      </div>
    </div>
  );
};

export default Sales;
