import React, { createContext, useState, useContext, useEffect } from 'react';
import axios from 'axios';

const API_URL = process.env.REACT_APP_API_URL || 'http://127.0.0.1:8000/api';

const AppContext = createContext();

export const useApp = () => useContext(AppContext);

export const AppProvider = ({ children }) => {
  const [products, setProducts] = useState([]);
  const [customers, setCustomers] = useState([]);
  const [sales, setSales] = useState([]);
  const [settings, setSettings] = useState({
    companyName: 'SmartStock',
    currency: 'USD',
    taxRate: 8.0
  });
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);
  const [dashboardStats, setDashboardStats] = useState(null);

  // Initialize axios
  const api = axios.create({
    baseURL: API_URL,
    headers: {
      'Content-Type': 'application/json',
    },
  });

  // timeout and basic error logging
  api.defaults.timeout = 10000;
  api.interceptors.response.use(
    response => response,
    error => {
      console.error('API request error:', error.message || error);
      return Promise.reject(error);
    }
  );

  // Load initial data
  useEffect(() => {
    loadDashboardData();
  }, []);

  const loadDashboardData = async () => {
    try {
      setLoading(true);
      const [productsRes, customersRes, salesRes, statsRes] = await Promise.all([
        api.get('/products'),
        api.get('/customers'),
        api.get('/sales/recent/5'),
        api.get('/sales/stats/dashboard')
      ]);

      setProducts(productsRes.data);
      setCustomers(customersRes.data);
      setSales(salesRes.data);
      setDashboardStats(statsRes.data);
    } catch (err) {
      setError(err.message || err.response?.data?.message || 'Failed to load data');
    } finally {
      setLoading(false);
    }
  };

  // Product methods
  const fetchProducts = async (filters = {}) => {
    try {
      const params = new URLSearchParams(filters);
      const response = await api.get(`/products?${params}`);
      setProducts(response.data);
      return response.data;
    } catch (err) {
      setError(err.response?.data?.message || 'Failed to fetch products');
      throw err;
    }
  };

  const createProduct = async (productData) => {
    try {
      const response = await api.post('/products', productData);
      setProducts(prev => [response.data, ...prev]);
      return response.data;
    } catch (err) {
      setError(err.response?.data?.message || 'Failed to create product');
      throw err;
    }
  };

  const updateProduct = async (id, productData) => {
    try {
      const response = await api.put(`/products/${id}`, productData);
      setProducts(prev => prev.map(p => p._id === id ? response.data : p));
      return response.data;
    } catch (err) {
      setError(err.response?.data?.message || 'Failed to update product');
      throw err;
    }
  };

  const deleteProduct = async (id) => {
    try {
      await api.delete(`/products/${id}`);
      setProducts(prev => prev.filter(p => p._id !== id));
    } catch (err) {
      setError(err.response?.data?.message || 'Failed to delete product');
      throw err;
    }
  };

  // Customer methods
  const fetchCustomers = async (filters = {}) => {
    try {
      const params = new URLSearchParams(filters);
      const response = await api.get(`/customers?${params}`);
      setCustomers(response.data);
      return response.data;
    } catch (err) {
      setError(err.response?.data?.message || 'Failed to fetch customers');
      throw err;
    }
  };

  const createCustomer = async (customerData) => {
    try {
      const response = await api.post('/customers', customerData);
      setCustomers(prev => [response.data, ...prev]);
      return response.data;
    } catch (err) {
      setError(err.response?.data?.message || 'Failed to create customer');
      throw err;
    }
  };

  const updateCustomer = async (id, customerData) => {
    try {
      const response = await api.put(`/customers/${id}`, customerData);
      setCustomers(prev => prev.map(c => c._id === id ? response.data : c));
      return response.data;
    } catch (err) {
      setError(err.response?.data?.message || 'Failed to update customer');
      throw err;
    }
  };

  const deleteCustomer = async (id) => {
    try {
      await api.delete(`/customers/${id}`);
      setCustomers(prev => prev.filter(c => c._id !== id));
    } catch (err) {
      setError(err.response?.data?.message || 'Failed to delete customer');
      throw err;
    }
  };

  // Sale methods
  const fetchSales = async (filters = {}) => {
    try {
      const params = new URLSearchParams(filters);
      const response = await api.get(`/sales?${params}`);
      setSales(response.data);
      return response.data;
    } catch (err) {
      setError(err.response?.data?.message || 'Failed to fetch sales');
      throw err;
    }
  };

  const createSale = async (saleData) => {
    try {
      const response = await api.post('/sales', saleData);
      setSales(prev => [response.data, ...prev]);
      
      // Refresh dashboard stats
      const statsRes = await api.get('/sales/stats/dashboard');
      setDashboardStats(statsRes.data);
      
      // Refresh products to update stock
      const productsRes = await api.get('/products');
      setProducts(productsRes.data);
      
      return response.data;
    } catch (err) {
      setError(err.response?.data?.message || 'Failed to create sale');
      throw err;
    }
  };

  // Report methods
  const fetchSalesTrend = async (days = 7) => {
    try {
      const response = await api.get(`/reports/sales-trend?days=${days}`);
      return response.data;
    } catch (err) {
      setError(err.response?.data?.message || 'Failed to fetch sales trend');
      throw err;
    }
  };

  const fetchSalesByCategory = async () => {
    try {
      const response = await api.get('/reports/sales-by-category');
      return response.data;
    } catch (err) {
      setError(err.response?.data?.message || 'Failed to fetch category sales');
      throw err;
    }
  };

  const fetchTopProducts = async (limit = 10) => {
    try {
      const response = await api.get(`/reports/top-products/${limit}`);
      return response.data;
    } catch (err) {
      setError(err.response?.data?.message || 'Failed to fetch top products');
      throw err;
    }
  };

  const exportData = async (type, format = 'csv') => {
    try {
      let data = [];
      let headers = [];

      switch (type) {
        case 'products':
          data = products || [];
          headers = ['SKU', 'Name', 'Category', 'Price', 'Stock', 'MinStock'];
          break;
        case 'customers':
          data = customers || [];
          headers = ['Name', 'Email', 'Phone', 'Address'];
          break;
        case 'sales':
          data = sales || [];
          headers = ['SaleNumber', 'Date', 'Customer', 'Total', 'PaymentStatus'];
          break;
        default:
          data = [];
      }

      const toCSV = (hdrs, rows) => {
        const escape = (v) => `"${String(v ?? '').replace(/"/g, '""')}"`;
        const out = [hdrs.join(',')];
        for (const r of rows) out.push(hdrs.map(h => escape(getCell(r, h))).join(','));
        return out.join('\n');
      };

      const getCell = (row, header) => {
        switch (header) {
          case 'SKU': return row.sku;
          case 'Name': return row.name;
          case 'Category': return row.category;
          case 'Price': return row.price;
          case 'Stock': return row.stock;
          case 'MinStock': return row.minStock ?? '';
          case 'Email': return row.email;
          case 'Phone': return row.phone;
          case 'Address': {
            if (!row.address) return '';
            if (typeof row.address === 'string') return row.address;
            const parts = [row.address.street, row.address.city, row.address.state, row.address.zipCode, row.address.country].filter(Boolean);
            return parts.join(', ');
          }
          case 'SaleNumber': return row.saleNumber || '';
          case 'Date': return row.saleDate ? new Date(row.saleDate).toLocaleString() : '';
          case 'Customer': return row.customer?.name || '';
          case 'Total': return row.total ?? '';
          case 'PaymentStatus': return row.paymentStatus || '';
          default: return row[header] ?? '';
        }
      };

      if (format === 'csv') {
        const csv = toCSV(headers, data);
        // Prepend UTF-8 BOM so Excel recognizes UTF-8 encoding
        const blob = new Blob(["\uFEFF" + csv], { type: 'text/csv;charset=utf-8;' });
        const url = URL.createObjectURL(blob);
        const a = document.createElement('a');
        a.href = url;
        a.download = `${type}-${Date.now()}.csv`;
        document.body.appendChild(a);
        a.click();
        a.remove();
        URL.revokeObjectURL(url);
        return true;
      }

      if (format === 'pdf') {
        const { jsPDF } = await import('jspdf');
        const doc = new jsPDF();
        const lineHeight = 8;
        let y = 15;
        doc.setFontSize(12);
        doc.text(`${type.toUpperCase()} Export`, 14, 12);

        // write headers
        doc.setFontSize(10);
        doc.text(headers.join(' | '), 14, y);
        y += lineHeight;

        for (const row of data) {
          const rowText = headers.map(h => String(getCell(row, h))).join(' | ');
          if (y > 280) {
            doc.addPage();
            y = 15;
          }
          doc.text(rowText, 14, y);
          y += lineHeight;
        }

        doc.save(`${type}-${Date.now()}.pdf`);
        return true;
      }

      return null;
    } catch (err) {
      setError(err.message || 'Failed to export data');
      throw err;
    }
  };

  const showAlert = (message, type = 'info') => {
    // You can implement a toast notification system here
    console.log(`[${type.toUpperCase()}] ${message}`);
  };

  const formatCurrency = (amount) => {
    return new Intl.NumberFormat('en-US', {
      style: 'currency',
      currency: settings.currency
    }).format(amount);
  };

  const value = {
    products,
    customers,
    sales,
    settings,
    dashboardStats,
    loading,
    error,
    fetchProducts,
    createProduct,
    updateProduct,
    deleteProduct,
    fetchCustomers,
    createCustomer,
    updateCustomer,
    deleteCustomer,
    fetchSales,
    createSale,
    fetchSalesTrend,
    fetchSalesByCategory,
    fetchTopProducts,
    exportData,
    showAlert,
    formatCurrency,
    loadDashboardData
  };

  return <AppContext.Provider value={value}>{children}</AppContext.Provider>;
};