import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { useApp } from '../context/AppContext';
import ProductModal from '../components/modals/ProductModal';
import BulkUpdateModal from '../components/modals/BulkUpdateModal';

const Products = () => {
  const { 
    products, 
    fetchProducts, 
    createProduct, 
    updateProduct, 
    deleteProduct,
    formatCurrency,
    exportData,
    showAlert 
  } = useApp();

  const navigate = useNavigate();

  const [filters, setFilters] = useState({
    search: '',
    category: '',
    lowStock: false,
    outOfStock: false
  });
  const [showProductModal, setShowProductModal] = useState(false);
  const [showBulkModal, setShowBulkModal] = useState(false);
  const [editingProduct, setEditingProduct] = useState(null);
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    loadProducts();
  }, [filters]);

  const loadProducts = async () => {
    setLoading(true);
    await fetchProducts(filters);
    setLoading(false);
  };

  const handleFilterChange = (e) => {
    const { name, value, type, checked } = e.target;
    setFilters(prev => ({
      ...prev,
      [name]: type === 'checkbox' ? checked : value
    }));
  };

  const clearFilters = () => {
    setFilters({
      search: '',
      category: '',
      lowStock: false,
      outOfStock: false
    });
  };

  const handleSaveProduct = async (productData) => {
    try {
      if (editingProduct) {
        await updateProduct(editingProduct._id, productData);
        showAlert('Product updated successfully!', 'success');
      } else {
        await createProduct(productData);
        showAlert('Product created successfully!', 'success');
      }
      setShowProductModal(false);
      setEditingProduct(null);
    } catch (error) {
      showAlert(error.message, 'danger');
    }
  };

  const handleDeleteProduct = async (id) => {
    if (window.confirm('Are you sure you want to delete this product?')) {
      try {
        await deleteProduct(id);
        showAlert('Product deleted successfully!', 'success');
      } catch (error) {
        showAlert(error.message, 'danger');
      }
    }
  };

  const handleExportProducts = async (format = 'csv') => {
    try {
      await exportData('products', format);
      showAlert(`Products exported (${format}) successfully!`, 'success');
    } catch (error) {
      showAlert(error.message, 'danger');
    }
  };

  const getStockStatus = (stock, minStock) => {
    if (stock === 0) return { label: 'Out of Stock', class: 'badge-danger' };
    if (stock <= minStock) return { label: 'Low Stock', class: 'badge-warning' };
    return { label: 'In Stock', class: 'badge-success' };
  };

  const categories = [...new Set(products.map(p => p.category))];

  return (
    <div id="products-page">
      <header className="main-header">
        <div className="d-flex justify-content-between align-items-center">
          <div>
            <h1 className="header-title">Products Management</h1>
            <p className="header-subtitle">Manage your inventory and stock levels</p>
          </div>
          <div className="d-flex gap-10">
            <div className="btn-group">
              <button className="btn btn-info" onClick={() => handleExportProducts('csv')}>
                <i className="fas fa-file-csv"></i> Export CSV
              </button>
              <button className="btn btn-info" onClick={() => handleExportProducts('pdf')}>
                <i className="fas fa-file-pdf"></i> Export PDF
              </button>
            </div>
            <button 
              className="btn btn-success" 
              onClick={() => {
                setEditingProduct(null);
                setShowProductModal(true);
              }}
            >
              <i className="fas fa-plus"></i> Add Product
            </button>
          </div>
        </div>
      </header>

      {/* Quick Actions */}
      <div className="card mb-20">
        <div className="card-header">
          <h3 className="card-title">Quick Actions</h3>
        </div>
        <div className="form-row">
          <button 
            className="btn btn-warning" 
            onClick={() => setFilters(prev => ({ ...prev, lowStock: true }))}
          >
            <i className="fas fa-exclamation-triangle"></i> View Low Stock
          </button>
          <button className="btn btn-primary" onClick={clearFilters}>
            <i className="fas fa-list"></i> View All Products
          </button>
          <button className="btn btn-success" onClick={() => navigate('/reports?type=inventory')}>
            <i className="fas fa-chart-bar"></i> Stock Report
          </button>
          <button className="btn btn-info" onClick={() => setShowBulkModal(true)}>
            <i className="fas fa-edit"></i> Bulk Update
          </button>
        </div>
      </div>

      {/* Filters */}
      <div className="card">
        <div className="card-header">
          <h3 className="card-title">Product Filters</h3>
          <button className="btn btn-sm btn-secondary" onClick={clearFilters}>
            <i className="fas fa-times"></i> Clear
          </button>
        </div>
        <div className="form-row">
          <div className="form-group">
            <input
              type="text"
              className="form-control"
              name="search"
              placeholder="Search products..."
              value={filters.search}
              onChange={handleFilterChange}
            />
          </div>
          <div className="form-group">
            <select
              className="form-control form-select"
              name="category"
              value={filters.category}
              onChange={handleFilterChange}
            >
              <option value="">All Categories</option>
              {categories.map(category => (
                <option key={category} value={category}>{category}</option>
              ))}
            </select>
          </div>
          <div className="form-group">
            <label className="form-check-label">
              <input
                type="checkbox"
                className="form-check-input"
                name="lowStock"
                checked={filters.lowStock}
                onChange={handleFilterChange}
              />
              Low Stock Only
            </label>
          </div>
          <div className="form-group">
            <label className="form-check-label">
              <input
                type="checkbox"
                className="form-check-input"
                name="outOfStock"
                checked={filters.outOfStock}
                onChange={handleFilterChange}
              />
              Out of Stock
            </label>
          </div>
        </div>
      </div>

      {/* Products Table */}
      <div className="card">
        <div className="card-header">
          <h3 className="card-title">Products List</h3>
          <div className="d-flex align-items-center gap-10">
            <span className="badge badge-success">{products.length} Products</span>
            <button 
              className="btn btn-sm btn-primary" 
              onClick={loadProducts}
              disabled={loading}
            >
              <i className="fas fa-sync"></i> Refresh
            </button>
          </div>
        </div>
        <div className="table-container">
          {loading ? (
            <div className="spinner"></div>
          ) : (
            <table className="table">
              <thead>
                <tr>
                  <th>SKU</th>
                  <th>Name</th>
                  <th>Category</th>
                  <th>Price</th>
                  <th>Stock</th>
                  <th>Status</th>
                  <th>Actions</th>
                </tr>
              </thead>
              <tbody>
                {products.map(product => {
                  const status = getStockStatus(product.stock, product.minStock);
                  return (
                    <tr key={product._id}>
                      <td>{product.sku}</td>
                      <td>{product.name}</td>
                      <td>{product.category}</td>
                      <td>{formatCurrency(product.price)}</td>
                      <td>
                        <span className={`badge ${status.class}`}>
                          {product.stock}
                        </span>
                      </td>
                      <td><span className={`badge ${status.class}`}>{status.label}</span></td>
                      <td>
                        <div className="d-flex gap-10">
                          <button 
                            className="btn btn-sm btn-primary"
                            onClick={() => {
                              setEditingProduct(product);
                              setShowProductModal(true);
                            }}
                          >
                            <i className="fas fa-edit"></i>
                          </button>
                          <button 
                            className="btn btn-sm btn-danger"
                            onClick={() => handleDeleteProduct(product._id)}
                          >
                            <i className="fas fa-trash"></i>
                          </button>
                          <button className="btn btn-sm btn-info">
                            <i className="fas fa-eye"></i>
                          </button>
                        </div>
                      </td>
                    </tr>
                  );
                })}
              </tbody>
            </table>
          )}
        </div>
      </div>

      {/* Modals */}
      {showProductModal && (
        <ProductModal
          product={editingProduct}
          onSave={handleSaveProduct}
          onClose={() => {
            setShowProductModal(false);
            setEditingProduct(null);
          }}
        />
      )}

      {showBulkModal && (
        <BulkUpdateModal
          categories={categories}
          onClose={() => setShowBulkModal(false)}
          onUpdate={() => {
            loadProducts();
            showAlert('Bulk update applied successfully!', 'success');
          }}
        />
      )}
    </div>
  );
};

export default Products;